.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

input {
    width: 50% !important;
}

.distractify {
    font-family: 'Ubuntu Mono', monospace;
    font-size: 4em;
    margin-top: 30px;
}

.button {
    margin-bottom: 20px;
}
div .container {
    text-align: center; 
}
.card {
    margin: 5px 5px;
    width: 300px;
    min-height: 346px;
    max-height: 346px;
    float: left;
}

.card-image {
    height: 200px;
}

.button {
    margin-top: 10px;
    margin-right: 10px;
}

.vid-title {
    margin-top: 10px;
    font-size: 1.1em;
    overflow-y: scroll;
    max-height: 50px;
}

.desc-area {
    margin-top: 10px;
    height: 60px; 
    overflow-y: scroll;
}

.vid-desc {
    font-size: 0.8em;
}

.vid-display {
    max-height: 225px;
    margin-left: auto; 
    margin-right: auto; 
}

.saveBtn {
    color: greenyellow !important; 
    font-size: 1.3em;
    position: absolute; 
    top: 5px; 
    right: 5px; 
}

.tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;
  }
  
  .tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
  
    /* Position the tooltip */
    position: absolute;
    z-index: 1;
  }
  
  .tooltip:hover .tooltiptext {
    visibility: visible;
  }

 
